.SubHero {
  width: 100%;
  height: calc(100vh - 61px);
}

.sub-hero__wrapper {
  width: 100%;
  height: 50%;
  position: relative;
}

.sub-hero__text {
  position: absolute;
  top: 0;
  color: #feffff;
  font-family: Outfit;
  font-size: 80px;
  font-weight: 500;
  transform: translate(0%, -50%);
}
.sub-hero__text.left {
  left: 80px;
  top: 50%;
}
.sub-hero__text.right {
  right: 80px;
  top: 50%;
}

.sub-hero__image {
  width: 100%;
  height: 100%;
  object-fit: cover;

  background: linear-gradient(
    180deg,
    #1d1d1d 0%,
    rgba(0, 0, 0, 0.543512) 47.17%,
    rgba(0, 0, 0, 0) 100%
  );
}

@media screen and (max-width: 599px) {
  .SubHero {
    height: 100%;
  }

  .sub-hero__wrapper {
    height: 100vh;
  }

  .sub-hero__text {
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    line-height: 100px;
    font-size: 7rem;
  }

  .sub-hero__text.left {
    left: 50%;
  }
  .sub-hero__text.right {
    right: inherit;
    left: 50%;
  }
}

